import swal from 'sweetalert2'

export default {
  install: (Vue) => {
    // sweetalert2 , 
    const extendSwal = swal.mixin({
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: 'rgb(140,212,245)',
      cancelButtonColor: 'rgb(193,193,193)'
    })

    // 
    Vue.swal = extendSwal
    // 
    Vue.prototype.$swal = extendSwal
  }
}
